import {Component,OnInit} from '@angular/core';
import {Router,RouteSegment} from '@angular/router';

import {Hero} from './hero';
import {HeroService} from '../service/hero.service';

@Component({
     selector:'my-heroes',
     templateUrl:"app/hero-portal/hero/heroes.component.html",
     styleUrls:["app/hero-portal/hero/heroes.component.css"] ,
     providers:[HeroService]
})

export class HeroesComponent implements OnInit{
    
    heroes:Hero[];
    selectedHero:Hero;
    
    constructor(private _heroService:HeroService,
                private _router:Router,
                private _segment:RouteSegment){
    }
   
    onSelect(hero:Hero){ this.selectedHero=hero;}
    getHeroes(){
      this._heroService.getHeroes().then( (heroes) => {this.heroes=heroes} );
    }
    ngOnInit(){
      this.getHeroes();
    }
    gotoDetail(){
      this._router.navigateByUrl(`/hero-portal/detail/${this.selectedHero.id}`);
    //  this._router.createUrlTree([`app/hero-portal/detail/${this.selectedHero.id}`], this._segment);
    }
}


 
 